import React from 'react';
import s from './Navbar.module.css';
import { NavLink } from 'react-router-dom';
import profileLogo from '../../assets/images/man.png';
import dialogdLogo from '../../assets/images/chat.png';
import usersLogo from '../../assets/images/avatar.png';
import settingsLogo from '../../assets/images/gear.png';

const Navbar = () => {
  return (
    <nav className={s.nav}>
      <div className={`${s.item} ${s.active}`}>
        <NavLink to='/profile' activeClassName={s.active}> <img className={s.logo} src={profileLogo}/>  Profile</NavLink>
      </div>
      <div className={s.item}>
        <NavLink to='/dialogs' activeClassName={s.active}>  <img className={s.logo} src={dialogdLogo}/>  Messages</NavLink>
      </div>
        <div className={s.item}>
        <NavLink to='/users' activeClassName={s.active}> <img className={s.logo} src={usersLogo}/>  Users</NavLink>
      </div>
    {/*  <div className={s.item}>
        <NavLink to='/news' activeClassName={s.active}>News</NavLink>
      </div>
      <div className={s.item}>
        <NavLink to='/music' activeClassName={s.active}>Music</NavLink>
      </div>*/}
      <div className={s.item}>
        <NavLink to='/settings' activeClassName={s.active}>  <img className={s.logo} src={settingsLogo}/>  Settings</NavLink>
      </div>
    </nav>
  )
}

export default Navbar;
