import React from 'react';
import s from './Header.module.css';
import {NavLink} from "react-router-dom";
import logoPhoto from '../../assets/images/2168070.png'
const Header = (props) => {
    return (
        <header className={s.header}>
            <img  className={s.logo} src={logoPhoto} />
            <div className={s.loginBlock}>
                {props.isAuth
                    ? <div>{props.login} - <button  className={s.buttons} onClick={props.logout}>Log out</button></div>
                    : <NavLink to={'/login'}>Login</NavLink>}
            </div>
        </header>
    )
}

export default Header;
